#common-menu .footer .f-ltd{color:#7d7d7d; font-size:rem(10px); margin-top:rem(20px); background:none;}
#common-menu.tod-common-menu .footer .f-bt .f-register:before{background:url("/img/en/ico_register.png") left 0 no-repeat; background-size:100% 100%;}
#common-menu.tod-common-menu .footer .f-bt .f-setting:before{background:url("/img/en/ico_setting.png") left 0 no-repeat; background-size:100% 100%;}
#common-menu.tod-common-menu.user-type.scroll .section-userinfo{height:rem(56px); box-shadow:none;}
#common-menu.tod-common-menu.user-type.scroll{padding-top:3.4375rem;}
#common-menu.tod-common-menu .c-close {background-image: url("/img/en/exit-white.png");}
#common-menu .section-cont .section-gnb .bt-depth1 > .more{background-image:url("/img/en/ico-menu-open.png");}
