#common-menu .section-cont .section-gnb .bt-depth1{min-height:rem(75px)}
#common-menu .section-cont .section-gnb .bt-depth1.line::before{
    content:''; display:inline-block; width:92%; height:1px; background:#e5e5e5; position:absolute; top:0; left:50%; transform:translateX(-50%);
}
#common-menu .section-cont .section-gnb .bt-depth1.line::after{
    content:''; display:inline-block; width:92%; height:1px; background:#e5e5e5; position:absolute; bottom:0; left:50%; transform:translateX(-50%);
}
#common-menu .section-cont .open .bt-depth1.line::after{
    display:none;
}
#common-menu .section-cont .section-gnb .bt-depth1 > .bt{min-height:rem(75px);}
#common-menu .section-cont .section-gnb .bt-depth1 > .bt .ti-info .info{display:block; margin-left:0; margin-top:rem(7.5px);}
#common-menu.tod-common-menu .footer .f-bt .f-login:before{background-image:url('/img/en/icon_32px_login.svg')}
#common-menu.tod-common-menu .footer .f-bt .f-logout:before {
