/*영문만 추가*/
.link-area{width:100%; position:relative; padding:rem(25px 15px 0 15px);}
.link-area .rang-kor{font-size:rem(13px); color:#666; text-decoration:underline; position:relative; padding-left:rem(30px); margin-left:rem(5px);}
.link-area .rang-kor::before{
    content:''; display:inline-block; width:rem(25px); height:rem(25px); background:url("/img/en/ico_kor.png") 0 0 no-repeat; background-size: 100% 100%; position:absolute; top:50%; transform:translateY(-50%); left:0;
}
.link-area{
    box-sizing:border-box;
    .f-site{
        width:rem(105px);
        height:rem(30px);
        line-height:rem(30px);
        font-size:rem(12px);
        color:#666;
        padding-top:0;
        background:url("/img/en/arr_Fsite.png") 90% center no-repeat;
        background-size:rem(12.5px) rem(7px);
        position:absolute;
        top:rem(25px);
        right:rem(15px);
    }
}
.date-graph-list{
    li{
        padding:rem(15px 0);
        .graph-list-wrap{
            width:100%;
            position:relative;
            display:inline-block;
        }
        .cont{
            width:calc(100% - 4.5rem);
            padding-left:rem(58px);
            float:left;
            .my-data{
                width:100%;
                height:rem(17px);
                display:inline-block;
                &::after{
                    clear:both;
                }
                dt{
                    width:50%;
                    font-size:rem(13px);
                    color:#666;
                    float:left;
                }
                dd{
                    width:50%;
                    font-size:rem(16px);
                    text-align:right;
                    float:right;
                    font-weight:600;
                    color:#858585;
                    margin-top:-2px;
                    &.red{
                        color:#f76252;
                    }
                    &.black{
                        color:#000;
                    }
                }
            }
        }
    }
}
.fs-info {
    font-size: rem(13px);
    color: #666;
    position: relative;
    padding:rem(0 0 15px 12.5px);
    line-height:rem(17px);
    &::before {
        content: '';
        display: inline-block;
        position: absolute;
        left: 0;
        top: 0.38rem;
        width:rem(4px);
        height:rem(4px);
        background: #959595;
        border-radius: 100%;
    }
}
.charge-line{
    position: relative;
    display: block;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    width: 100%;
    padding: rem(20px 0);
    text-align: left;
    font-size: 0;
    line-height: 1;
    color: #000000;
    .tit{
        display: table-cell;
        font-size: 1rem;
        vertical-align: middle;
        font-weight:500;
    }
    &::after{
        position: absolute;
        right: 0;
        top: 50%;
        width: 1rem;
        height: 1rem;
        -webkit-transform: translateY(-50%);
        transform: translateY(-50%);
        content: '';
        background: url("/img/en/icon_32px_arrowmore.svg") no-repeat 23% 50%;
        background-size: 100%;
    }
}
